// Colors
$color--primary: #18cfab;
$color--skyblue: #00cbf6;
$color--darkblue: #2e4a5b;
$color--text: #777777;

// Fonts
$font--montserrat: 'Montserrat';
$font--alternative: sans-serif;

// Transitions
$transition--text: .2s;
$transition--button: .3s;
$transition--opacity: .3s;

// Breakpoints (Bootstrap 4)
$breakpoint--sm: 576px;
$breakpoint--md: 768px;
$breakpoint--lg: 992px;
$breakpoint--xl: 1200px;
