.section--services {
  .services {
    display: flex;
    flex-direction: column;

    .tabs--services {
      display: flex;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;

      .tab {
        cursor: pointer;

        display: flex;
        align-items: center;
        justify-content: center;

        width: calc(100% / 6);
        height: 70px;

        font-size: 16px;
        text-transform: capitalize;
        text-align: center;

        background-color: #f8fcfe;

        outline: 1px solid #ebebeb;

        @media screen and (max-width: $breakpoint--xl) {
          font-size: 14px; }

        @media screen and (max-width: $breakpoint--md) {
          width: calc(100% / 3);
          height: 50px; }

        @media screen and (max-width: $breakpoint--sm) {
          font-size: 12px; }

        @media screen and (min-width: $breakpoint--md) {
          &:hover {
            background-color: darken(#f8fcfe, 5%); } }

        &.active {
          position: relative;

          color: #fff;
          background-color: $color--primary;

          &::after {
            content: '';

            position: absolute;
            bottom: 0;
            left: 50%;

            width: 15px;
            height: 15px;

            background-color: $color--primary;

            transform: translate(-50%, 50%) rotate(45deg); } } } }

    .services__content {
      margin-top: 25px;

      .area {
        display: none;
        flex-direction: row;
        align-items: center;

        @media screen and (max-width: $breakpoint--md) {
          flex-direction: column; }

        @media screen and (max-width: $breakpoint--sm) {
          font-size: 12px; }

        &.active {
          display: flex; }

        .area__thumbnail {
          min-width: 200px;
          height: 140px;

          margin-right: 20px;

          @media screen and (max-width: $breakpoint--md) {
            max-width: 300px;
            height: inherit;

            margin: 0 20px 20px 20px; }

          img {
            width: 100%;
            height: 100%;

            object-fit: cover;
            object-position: center;

            overflow: hidden; } }

        .area__text {
          margin: 0; } } } } }

