.section--banner {
  position: relative;

  height: 100vh;

  padding: 0;

  color: #fff;

  text-align: center;

  background-image: url('../img/banner.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  & * {
    z-index: 1; }

  &::after {
    content: '';

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;

    width: 100%;
    height: 100%;

    background-color:#17171799 {} }

  .banner__subheading {
    font-size: 35px;
    font-weight: bold;

    @media screen and (max-width: $breakpoint--xl) {
      font-size: 28px; }

    @media screen and (max-width: $breakpoint--md) {
      font-size: 22px; }

    @media screen and (max-width: $breakpoint--sm) {
      font-size: 18px; }

    span {
      color: $color--primary; } }

  .banner__heading {
    margin-top: 15px;

    font-size: 79px;
    font-weight: bold;

    @media screen and (max-width: $breakpoint--xl) {
      font-size: 60px;

      margin-top: 5px; }

    @media screen and (max-width: $breakpoint--md) {
      font-size: 50px; }

    @media screen and (max-width: $breakpoint--sm) {
      font-size: 40px; } }

  .banner__excerpt {
    font-size: 34px;
    font-weight: 300;
    text-transform: capitalize;

    color: #d4d4d4;

    @media screen and (max-width: $breakpoint--xl) {
      font-size: 26px; }

    @media screen and (max-width: $breakpoint--md) {
      font-size: 20px; }

    @media screen and (max-width: $breakpoint--sm) {
      margin-top: 10px;

      font-size: 14px; } }

  .banner__buttons {
    margin-top: 140px;

    @media screen and (max-width: $breakpoint--xl) {
      margin-top: 70px; }

    @media screen and (max-width: $breakpoint--md) {
      margin-top: 40px; }

    .button + .button {
      margin-left: 30px;

      @media screen and (max-width: $breakpoint--sm) {
        margin-left: 20px; } } } }
