.section--purchase {
  padding: 120px 0;

  background-color: #40474b;

  @media screen and (max-width: $breakpoint--md) {
    padding: 80px 0; }

  .section__inner {
    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;

    h2 {
      font-size: 38px;
      font-weight: bold;
      text-transform: uppercase;

      color: $color--primary;

      @media screen and (max-width: $breakpoint--md) {
        font-size: 28px; } }

    p {
      font-size: 16px;

      margin: 20px 0 0 0;

      color: #d8d8d8;

      @media screen and (max-width: $breakpoint--md) {
        font-size: 14px; } }

    .button {
      margin-top: 60px;

      @media screen and (max-width: $breakpoint--md) {
        margin-top: 40px; } } } }
