.post {
  cursor: pointer;

  position: relative;

  display: flex;
  flex-direction: column;

  margin-bottom: 30px;

  @media screen and (max-width: $breakpoint--sm) {
    max-width: 250px;

    margin-left: auto;
    margin-right: auto; }

  &:hover {
    .post__title {
      color: $color--primary; }

    .post__date {
      background-color: $color--primary; } }

  &__thumbnail {
    width: 100%;
    height: 225px;

    @media screen and (max-width: $breakpoint--sm) {
      height: 175px; }

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
      object-position: center;

      overflow: hidden; } }

  &__content {
    padding: 10px 15px;

    background-color: #f8fcfe;

    outline: 1px solid #ebebeb;

    .post__title {
      font-size: 18px;
      font-weight: bold;
      text-transform: capitalize;

      transition: color $transition--text;

      @media screen and (max-width: $breakpoint--xl) {
        font-size: 16px; }

      @media screen and (max-width: $breakpoint--xl) {
        font-size: 14px; } }

    .post__info {
      margin-top: 5px;

      font-weight: 300;

      @media screen and (max-width: $breakpoint--xl) {
        font-size: 12px; }

      span {
        margin-right: 15px;

        &:last-child {
          margin-right: 0; } } } }

  &__date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: 0;
    right: 0;

    width: 70px;
    height: 70px;

    font-size: 16px;

    color: #fff;
    background-color: #203e38;

    transition: background-color $transition--text;

    @media screen and (max-width: $breakpoint--xl) {
      width: 60px;
      height: 60px; }

    @media screen and (max-width: $breakpoint--sm) {
      width: 50px;
      height: 50px;

      font-size: 14px; }

    span + span {
      margin-top: 5px;

      @media screen and (max-width: $breakpoint--sm) {
        margin-top: 0; } } }

  &--small {
    display: flex;
    flex-direction: row;

    @media screen and (max-width: $breakpoint--sm) {
      flex-direction: column;
      align-items: center;

      text-align: center;

      margin: 0 auto; }

    .post__thumbnail {
      max-width: 77px;
      height: 66px;

      margin-right: 15px;

      @media screen and (max-width: $breakpoint--sm) {
        margin-bottom: 10px; } }

    .post__content {
      display: flex;
      flex-direction: column;

      padding: 0;

      background-color: transparent;

      outline: none;

      .post__title {
        font-size: 16px;
        text-transform: uppercase;

        margin-bottom: 10px;

        @media screen and (max-width: $breakpoint--md) {
          font-size: 14px;

          margin-bottom: 5px; } }

      .post__excerpt {
        margin-bottom: 10px;

        @media screen and (max-width: $breakpoint--md) {
          font-size: 12px;

          margin-bottom: 5px; } }

      .post__date {
        display: block;

        position: static;

        width: auto;
        height: auto;

        background-color: transparent;

        @media screen and (max-width: $breakpoint--md) {
          font-size: 12px; } } } } }
